import get from 'lodash/get';
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import Image from 'components/Image';
import { getImage } from 'constants/images';

import styles from './Landing.module.scss';

// import Navbar from './components/Navbar';
const Navbar = React.lazy(() => import('./components/Navbar'));
const Splash = React.lazy(() => import('./components/Splash'));
const Portfolio = React.lazy(() => import('./components/Portfolio'));
const DigitalMarketing = React.lazy(() => import('./components/DigitalMarketing'));
const ProductionProcess = React.lazy(() => import('./components/ProductionProcess'));
const WhyInfiniteFrontier = React.lazy(() => import('./components/WhyInfiniteFrontier'));
const Faq = React.lazy(() => import('./components/Faq'));
const Footer = React.lazy(() => import('./components/Footer'));



const splash = getImage('landing.splash');
const greenWavy = getImage('landing.green_wavy');
const ellipse = getImage('landing.ellipse');

class Landing extends Component {
  componentDidMount() {
    const { location } = this.props;
    const hash = get(location, 'state.hash');
    this.updateHash();

    if (!hash) {
      window.scrollTo(0, 0);
    }
  }

  componentDidUpdate(prevProps) {
    const { location } = this.props;
    const hash = get(location, 'state.hash');


    if (hash && hash !== get(prevProps, 'location.state.hash')) {
      this.updateHash();
    }
  }

  updateHash() {
    const { location, history } = this.props;
    const hash = get(location, 'state.hash');

    if (hash) {
      try {
        if (hash === 'home') {
          window.scrollTo({ top: 0, left: 0 });
        } else {
          const ele = document.getElementById(hash);
          const bodyRect = document.body.getBoundingClientRect();
          const elemRect = ele.getBoundingClientRect();
          const offset = elemRect.top - bodyRect.top - 88;
          if (ele) {
            window.scrollTo({ top: offset });
          }
        }

        let state = { ...history.location.state };
        delete state.hash;
        history.replace({ ...history.location, state });
      } catch (error) {
        // eslint-disable-next-line
      }
    }
  }

  render() {
    return (
      <React.Suspense fallback={<div />}>
        <div className={styles.landing}>
          <div className={styles.landingSplash}>
            <Image {...splash} />
          </div>
          <div className={styles.greenWavy}>
            <Image {...greenWavy} />
          </div>
          <div className={styles.ellipse}>
            <Image {...ellipse} />
          </div>
          <Navbar />
          <Splash />
          <Portfolio />
          <DigitalMarketing />
          <ProductionProcess />
          <WhyInfiniteFrontier />
          <Faq />
          <Footer />
        </div>
      </React.Suspense>
    );
  }
}

Landing.defaultProps = {
};

Landing.propTypes = {
  location: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};


export default withRouter(Landing);
